import styles from "./Skills.module.css";
import React from "react";
import TextHeading from "../Components/Heading/TextHeading";
import SkillCard from "./SkillCard";
import { techSkills } from "../utils/constant";
import GithubStats from "./GithubStats";
import GitHubCalendars from "./GitHubCalendar";

function Skills({ innerWidth }) {
  return (
    <section id="skills">
      <TextHeading cont="Skills" />
      <div className={styles.container1}>
        <div className={styles.main}>
          {techSkills.length > 0 &&
            techSkills?.map((item, index) => {
              return <SkillCard key={index} {...item} />;
            })}
        </div>
      </div>
      <div className={styles.container2}>
        <TextHeading cont="Github Stats" />
        <GithubStats />
        <GitHubCalendars innerWidth={innerWidth} />
      </div>
    </section>
  );
}

export default Skills;
