import React from "react";
import GitHubCalendar from "react-github-calendar";
import ReactTooltip from "react-tooltip";
import styles from "./Github.module.css";
//

const GitHubCalendars = ({ innerWidth }) => {
  const selectLastHalfYear = (contributions) => {
    const currentYear = new Date().getFullYear();
    const currentMonth = new Date().getMonth();
    const shownMonths = 10;

    return contributions.filter((day) => {
      const date = new Date(day.date);
      const monthOfDay = date.getMonth();

      return (
        date.getFullYear() === currentYear &&
        monthOfDay > currentMonth - shownMonths &&
        monthOfDay <= currentMonth
      );
    });
  };
  // console.log(styles.calendar);

  return (
    <div className={styles.calendar}>
      <GitHubCalendar
        username="sunilrath101"
        year={2022}
        transformData={selectLastHalfYear}
        blockMargin={innerWidth < 1500 ? 2 : 3}
        blockSize={
          innerWidth < 475
            ? 10
            : innerWidth < 700
            ? 13
            : innerWidth < 1500
            ? 18
            : 40
        }
        fontSize={innerWidth < 700 ? 10 : innerWidth < 1500 ? 18 : 27}
      >
        <ReactTooltip
          delayShow={15}
          textColor="#64ffda"
          backgroundColor="#0a192f"
          padding="15px"
          offset={{ top: 10, left: 10 }}
          html
        />
      </GitHubCalendar>
      {/* <div>
        <h2>Width: {windowSize.innerWidth}</h2>

        <h2>Height: {windowSize.innerHeight}</h2>
      </div> */}
    </div>
  );
};

export default GitHubCalendars;
