import { useEffect, useState } from "react";
import About from "./About/About";
import "./App.css";
import Contact from "./Components/Contact/Contact";
import Footer from "./Components/Footer/Footer";
import Backdrop from "./Components/Navbar/Backdrop";
import Navbar from "./Components/Navbar/Navbar";
import SideDrawer from "./Components/Navbar/SideDrawer";
import ContactMe from "./ContactMe/ContactMe";
import Home from "./Home/Home";
import Projects from "./Projects/Projects";
import Skills from "./Skills/Skills";

function getWindowSize() {
  const { innerWidth, innerHeight } = window;
  return { innerWidth, innerHeight };
}

function App() {
  const [toggle, setToggle] = useState(false);
  const [windowSize, setWindowSize] = useState(getWindowSize());
  const { innerWidth } = windowSize; //innerHeight

  const toggleHandler = () => {
    setToggle((prev) => !prev);
  };
  useEffect(() => {
    function handleWindowResize() {
      setWindowSize(getWindowSize());
    }

    window.addEventListener("resize", handleWindowResize);

    return () => {
      window.removeEventListener("resize", handleWindowResize);
    };
  }, []);

  useEffect(() => {
    const tlg = () => {
      if (innerWidth > 601) {
        setToggle(false);
      }
    };

    // console.log("object");
    return tlg;
  }, [innerWidth]);
  return (
    <>
      <main>
        <Navbar toggleHandler={toggleHandler} toggle={toggle} />
        {innerWidth < 601 && toggle && (
          <SideDrawer toggleHandler={toggleHandler} />
        )}
        {innerWidth < 601 && toggle && (
          <Backdrop toggleHandler={toggleHandler} />
        )}

        <div className="container">
          <Home />
          <About />
          <Skills innerWidth={innerWidth} />
          <Projects />
          <ContactMe />
        </div>

        <Contact />
        <Footer />
      </main>
    </>
  );
}

export default App;
