import styles from "./About.module.css";
import React from "react";
import TextHeading from "../Components/Heading/TextHeading";

const About = () => {
  return (
    <section id="about">
      <TextHeading cont="About Me" />
      <div className={styles.container}>
        <div className={styles.left}>
          <p className={styles.desc}>
            Adaptable and responsible web developer capable of writing
            production-ready code using HTML, CSS, JavaScript, React.js on the
            Frontend and Node.js, and Expres.js on the Backend to build
            single-page applications. Looking forward to working in a fastpaced
            environment and playing a key role in a company's growth. Hope to
            work in a challenging environment as a specialized MERN Stack
            Developer
          </p>
        </div>
        <div className={styles.right}>
          <img
            src="https://avatars.githubusercontent.com/u/107469021?v=4"
            alt="profile"
            className={styles.profile_img}
          />
          <div className={styles.img_border}></div>
        </div>
      </div>
    </section>
  );
};

export default About;
