import React from "react";
import styles from "./SideDrawer.module.css";
import { Link } from "react-scroll";
import resume from "../../assets/resume/Sunil_Singh Rathor_Resume_20-11-2022-21-33-54.pdf";
import CloseIcon from "@mui/icons-material/Close";

const SideDrawer = ({ toggleHandler }) => {
  return (
    <nav className={styles.sideBar}>
      <div>
        <CloseIcon
          className={styles.close}
          sx={{ fontSize: 36 }}
          onClick={() => toggleHandler()}
        />
      </div>
      <div className={styles.linkDiv}>
        <Link to="about" spy offset={-50} className={styles.link}>
          About
        </Link>

        <Link to="skills" offset={-50} spy className={styles.link}>
          Skills
        </Link>

        <Link to="projects" spy offset={-50} className={styles.link}>
          Projects
        </Link>

        <Link to="contact" spy offset={-50} className={styles.link}>
          Contact
        </Link>

        <a href={resume} download>
          <button className={styles.resumeBtn}>Resume</button>
        </a>
      </div>
    </nav>
  );
};

export default SideDrawer;
