import React from "react";
import styles from "./ContactMe.module.css";
import TextHeading from "../Components/Heading/TextHeading";
import Mail from "@mui/icons-material/Mail";
import Twitter from "@mui/icons-material/Twitter";
import GitHub from "@mui/icons-material/GitHub";
import LinkedIn from "@mui/icons-material/LinkedIn";
import MobileFriendlyTwoTone from "@mui/icons-material/MobileFriendlyTwoTone";
import { send } from "@emailjs/browser";
import { useState } from "react";
import { ToastContainer, toast } from "react-toastify";
import "react-toastify/dist/ReactToastify.css";

const ContactMe = () => {
  const [formData, setFormData] = useState({
    from_name: "",
    email_id: "",
    subject: "",
    message: "",
  });

  const toastError = {
    position: "bottom-right",
    autoClose: 8000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "light",
  };

  const toastSuccess = {
    position: "bottom-right",
    autoClose: 8000,
    hideProgressBar: false,
    closeOnClick: true,
    pauseOnHover: true,
    draggable: true,
    progress: undefined,
    theme: "light",
  };

  const changehandler = (e) => {
    const { value, name } = e.target;
    setFormData({ ...formData, [name]: value });
  };

  const submitHandler = (e) => {
    e.preventDefault();
    const { from_name, email_id, subject, message } = formData;
    if (
      from_name === "" ||
      email_id === "" ||
      subject === "" ||
      message === ""
    ) {
      toast.error("Please fill all the Credentials", toastError);
    } else {
      send("service_jwbfygs", "template_mbjyoxx", formData, "5XnJBGrQhioeG80SC")
        .then((res) => {
          console.log(res);
          toast.success("Email Sent", toastSuccess);
        })
        .catch((err) => {
          toast.error("Something Went Wrong,Try Again", toastError);
          console.log(err);
        });
    }
  };

  return (
    <section id="contact">
      <TextHeading cont="Contact Me" />

      <div className={styles.project}>
        <div className={styles.left}>
          <h1 className={styles.heading_2}>Get In Touch</h1>
          <p className={styles.desc}>
            Feel free to get in touch with me. I am always open to discussing
            new projects, creative ideas or opportunities to be part of your
            visions.
          </p>
          <div className={styles.contacts}>
            <a href="mailto:sunilrath.ssr@gmail.com" className={styles.contact}>
              <Mail />
              <span>sunilrath.ssr@gmail.com</span>
            </a>
            <br />
            <a href="tel:+918947816217" className={styles.contact}>
              <MobileFriendlyTwoTone />
              <span>8947816217</span>
            </a>
            <br />
            <a
              href="https://www.linkedin.com/in/sunil-singh-rathore-5834b4239/"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <LinkedIn />
              <span>sunil-singh-rathore-5834b4239</span>
            </a>
            <br />
            <a
              href="https://github.com/Sunilrath101"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <GitHub />
              <span>sunilrath101</span>
            </a>
            <br />
            <a
              href="https://twitter.com/SunilRath101"
              target="_blank"
              rel="noopener noreferrer"
              className={`${styles.contact} ${styles.block}`}
            >
              <Twitter />
              <span>SunilRath101</span>
            </a>
          </div>
        </div>
        <div className={styles.right}>
          <form onSubmit={submitHandler} className={styles.form}>
            <input
              type="text"
              name="from_name"
              placeholder={"Your Name"}
              onChange={changehandler}
              required
            />
            <input
              type="email"
              name="email_id"
              placeholder={"Email"}
              onChange={changehandler}
              required
            />
            <input
              type="text"
              name="subject"
              placeholder={"Subject"}
              onChange={changehandler}
              required
            />
            <textarea
              rows="4"
              type="text"
              name="message"
              onChange={changehandler}
              placeholder={"Write your message here"}
              required
            />

            <button className={styles.btn} type="submit">
              Send message
            </button>
          </form>
        </div>
      </div>
      <ToastContainer
        position="bottom-right"
        autoClose={8000}
        hideProgressBar={false}
        newestOnTop={false}
        closeOnClick
        rtl={false}
        pauseOnFocusLoss
        draggable
        pauseOnHover
        theme="light"
      />
    </section>
  );
};

export default ContactMe;
