import { TypeAnimation } from "react-type-animation";
import styles from "./Home.module.css";

const Name = () => {
  return (
    <h1 className={styles.name}>
      <TypeAnimation
        sequence={[
          "I'm Full Stack Web Developer",
          1000,
          "I'm Problem Solver",
          1000,
          "I'm MERN Developer",
          1000,
        ]}
        speed={50}
        wrapper="span"
        repeat={Infinity}
      />
    </h1>
  );
};

export default Name;
