import React from "react";
import styles from "./Navbar.module.css";
import { Link } from "react-scroll";
import resume from "../../assets/resume/Sunil_Singh Rathor_Resume_20-11-2022-21-33-54.pdf";
import MenuIcon from "@mui/icons-material/Menu";
// import MenuOpenIcon from "@mui/icons-material/MenuOpen";
const Navbar = ({ toggleHandler, toggle }) => {
  return (
    <div>
      <nav className={styles.container}>
        <div className={styles.left}>
          <Link to="home" activeClass="active" spy className={styles.link}>
            Sunil
          </Link>
        </div>
        <div className={styles.right}>
          <Link to="about" spy offset={-50} className={styles.link}>
            About
          </Link>
          <Link to="skills" offset={-50} spy className={styles.link}>
            Skills
          </Link>
          <Link to="projects" spy offset={-50} className={styles.link}>
            Projects
          </Link>
          <Link to="contact" spy offset={-50} className={styles.link}>
            Contact
          </Link>
          <a href={resume} download>
            <button className={styles.resumeBtn}>Resume</button>
          </a>
        </div>
        <div className={styles.right1}>
          {!toggle ? (
            <MenuIcon sx={{ fontSize: 50 }} onClick={() => toggleHandler()} />
          ) : null}
        </div>
      </nav>
    </div>
  );
};

export default Navbar;
